.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

h1 {
  margin-bottom: 20px;
  font-size: 2.5rem;
  color: #61dafb;
}

.game-info {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
  font-size: 1.2rem;
}

.start-button {
  background-color: #61dafb;
  border: none;
  color: #282c34;
  padding: 15px 30px;
  font-size: 1.2rem;
  border-radius: 25px;
  cursor: pointer;
  margin-bottom: 30px;
  transition: all 0.3s ease;
}

.start-button:hover {
  background-color: #4fa8c5;
  transform: scale(1.05);
}

.game-board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}

.mole-hole {
  width: 100px;
  height: 100px;
  background-color: #8b4513;
  border-radius: 50%;
  position: relative;
  cursor: pointer;
  border: 3px solid #654321;
  transition: all 0.2s ease;
}

.mole-hole:hover {
  transform: scale(1.05);
}

.mole-hole.active {
  background-color: #a0522d;
}

.mole {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  animation: pop 0.3s ease;
}

@keyframes pop {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.2);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}

.game-over {
  margin-top: 30px;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  border: 2px solid #61dafb;
}

.game-over h2 {
  color: #ff6b6b;
  margin-bottom: 10px;
}

.game-over p {
  font-size: 1.3rem;
  color: #61dafb;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
